.modal {
  top: 250px;
  position: relative;
  // position: absolute;
  // top: 560px;
  // top: 55%;
  // left: 50%;
  // transform: translateX(-50%) translateY(-50%);
  @media screen and (min-width: $tablet) {
    top: 0;

  }

  text-align: center;

  width: 280px;
  min-height: 991px;

  background-color: #FFFFFF;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  outline: 2px solid #F8D24E;

  transform: scale(1);
  transition: transform $anim-std,
    opacity $anim-std;
    

  .backdrop.is-hidden & {
    opacity: 1;
    transform: scale(0);
  }
  @media screen and (min-width: 768px) {
    width: 568px;
    min-height: 644px;
  }
}

.modal-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  padding: 0;
  margin: 0;
  border-radius: 50%;
  border: none;
  background-color: transparent;

  width: 20px;
  height: 20px;
  cursor: pointer;

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

  display: flex;
  align-items: center;
  justify-content: center;

  &:hover, &:focus {
    fill: $accent-color;
  }
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.modal__container{
    padding-top: 28px;
    padding-bottom: 29px;
    padding-left: 28px;
    padding-right: 28px;
    @media screen and (min-width: 768px) {
      padding-top: 32px;
      padding-bottom: 32px;
      padding-left: 54px;
      padding-right: 54px;
    }
}
