.modal__items--green .checkbox-green {
  &::after {
      content: '';
      position: absolute;
      display: inline-block;
      z-index: -11;
      border: 2px solid rgba(150, 175, 140, 1);
      border-radius: 10px;
  
      width: 132px;
      height: 136px;

      @media screen and (min-width: 768px) {
        top: 35%;
        left: 0;
      }
  
      top: 35%;
      left: 46px
    }
  
    &::before {
      content: '';
      position: absolute;
      z-index: -10;

      @media screen and (min-width: 768px) {
        top: 7px;
        left: 0;
      }
      top: 7px;
      left: 46px;
  
