.checkbox_g, .checkbox_y, .checkbox_r{
  appearance: none;
}
.checkbox_g:checked ~ .checkbox-green::after{
  background-color: rgba(150, 175, 140, 1);
}
.checkbox_y:checked~.checkbox-yellow::after {
  background-color: rgba(239, 212, 120, 1);
}
.checkbox_r:checked~.checkbox-red::after {
  background-color: rgba(214, 147, 109, 1);
}
